body{
  background-color: rgb(31, 31, 31);

}

.about-us {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

#welcome {
  font-size: 2em;
  color: #ffffff;
}

#paragrafi {
  margin-bottom: 15px;
  line-height: 1.5;
  color: #f5f5f5;
}

.image-container {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

#imazhi {
  max-width: 30%;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

#img:hover {
  transform: scale(1.1);
}

.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.lightbox img {
  max-width: 80%;
  max-height: 80%;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.home-link {
  display: flex;
  text-align: center;
  justify-content: center;
  margin-top: 17vh;
  padding: 10px 15px;
  background-color: #4caf50;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
}

.home-link:hover {
  background-color: #45a049;
}

iframe {
  width: 100%;
  height: 400px;
  border: 1px solid #ddd;
  border-radius: 8px; 
  margin-top: 10vh;
}

@media (max-width: 600px) {
  iframe {
    height: 300px;
  }
}

.reviews-section {
  margin-top: 30px;
}

.review {
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 20px;
}

.review-content {
  font-size: 16px;
  margin-bottom: 10px;
}

.review-author {
  font-style: italic;
  font-size: 14px;
  color: #666;
}

/* Footer styles */
.footer {
  margin-top: 30px;
  text-align: center;
  background-color: #f2f2f2;
  padding: 10px;
}

